;(function(window, $, undefined){

	APP.catch = {}

	$(document).ready(function(){

		console.log('ready')

		var cont = $('#catch')

		var act = cont.find('span')
		var title = cont.find('h1')
		var desc = cont.find('p')

		var svgFound = cont.find('#svgFound')

		var ball = svgFound.find('#ball').css({opacity:0, scale: 0, transformOrigin: '50% 50%'})
		var imgs = svgFound.find('#imgs').css({opacity:0})

		var width_view = $(window).width()
        var width_content = svgFound.width()
        svgFound.css({x:(width_content-width_view)/2*-1})

		var p = cont.find('.middle .wrapper > *').css({opacity:0})

		function show(){
			showTextOnly()

			ball.css({opacity:1})
			ball.transition({scale:1, transformOrigin: '50% 50%', complete:function(){
				ball.transition({opacity:0}, 250)
			}}, 1000, 'easeInOutQuart')
			
			imgs.transition({opacity:1, delay:1000}, 0)
		}

		function showTextOnly(){
			p.each(function(i, e){
				$(this).transition({opacity:1, delay:i*200}, 1000)
			})
			cont.removeClass('bypass')
		}




		function hide(){
			p.each(function(i, e){
				$(this).transition({opacity:0}, 1000)
			})
			cont.addClass('bypass')

			ball.css({opacity:0, scale:0})
			imgs.transition({opacity:0}, 500)
		}

		function setTitle(t){
			title.text(t)
		}

		function setText(t){
			desc.text(t)
		}

		function setAct(t){
			act.text(t)
			
		}

		APP.catch.showTextOnly = showTextOnly
		APP.catch.setTitle = setTitle
		APP.catch.setAct = setAct
		APP.catch.setText = setText
		APP.catch.hide = hide
		APP.catch.show = show
	})



})(window, jQuery)