;(function(window, $, undefined){

	APP.panel = {}

	$(document).ready(function(){

		var cont = $('#panel')
		var title = cont.find('[js_title]')
		var action = cont.find('[js_action]')
		var btn = cont.find('#btnPlay')
		var itms = cont.find('[js_items]')
		var itms_best = cont.find('[js_items_best]')
		var ribb = cont.find('#ribbonSvg')
		var targetsSvg = cont.find('.target')

		targetsSvg.find('svg').attr('viewBox', '90 0 250 400')

		function show(delay){
			var d = (delay) ? delay : 0
			//cont.show()
			cont.transition({y:0, delay:d}, 1500, 'easeInOutQuint')
		}

		function hide(){
			//cont.hide()
			var h = cont.height()
			cont.transition({y:h}, 0, 'easeInOutQuint')
		}

		function setTarget(id){
			var targets = targetsSvg.find('svg > g')
			targets.hide()

			if(id>=0){
				var itm = targets.get(id)
				$(itm).show()
			}
		}

		function setTitle(v){
			title.html(v)
		}

		function setAction(v){
			console.log('setAction', v)
			action.text(v)
		}

		function setButton(url){
			btn.attr('href', url)
		}

		function setButtonLabel(v){
			btn.text(v)
		}


		function setItemsNum(v){
			itms.text(v)
		}

		function setRibbon(v){
			var over = ribb.find('#gameover').hide()
			var win = ribb.find('#win').hide()
			var next = ribb.find('#next').hide()
			var logo = ribb.find('#logopositive').hide()
			var oversvg = cont.find('#overSvg').hide()

			if(v==0) next.show()
			if(v==1) over.show()
			if(v==2) win.show()

			if(v>0) logo.show() 

			if(v==1) oversvg.show()
		}


	
		APP.panel.setButtonLabel = setButtonLabel
		APP.panel.setRibbon = setRibbon
		APP.panel.setItemsNum = setItemsNum
		APP.panel.setButton = setButton
		APP.panel.setAction = setAction
		APP.panel.setTitle = setTitle
		APP.panel.setTarget = setTarget
		APP.panel.show = show
		APP.panel.hide = hide
	})



})(window, jQuery)