;(function(window, $, undefined){

	window.APP = {
		game: new GamePlay()
	}

	$(document).ready(function(){

		$.get('assets/data.csv', function(data){
			APP.db = d3.csvParse(data)
			console.log(APP.db)

			$.get('assets/labels.csv', function(data){
				var csv = d3.csvParse(data)

				APP.labels = {}

				csv.forEach(function(d, i){
					APP.labels[d.Label_id] = d.Text
				})

				console.log(APP.labels)

				csv.forEach(function(d, i){
					$('['+d.Label_id+']').html(d.Text)
				})
			})
		})

	})

	APP.getDato = function(){
		var dato = null
		var name = APP.game.getCurrentName()
		APP.db.forEach(function(d){
			var n = d.layer_name.replace(/_\d_/g, '')
			console.log(n, name)
			if(n == name) dato = d
		})
		return dato
	}


})(window, jQuery)